import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: '登录',
    component: () => import('../views/login/index.vue'),
    meta: {
      TabbarShow: false // 需要显示 底部导航
    }
  },
  {
    path: '/registered',
    name: '注册',
    component: () => import('../views/login/registered.vue'),
    meta: {
      TabbarShow: false // 需要显示 底部导航
    }
  },
  {
    path: '/',
    name: '首页',
    component: () => import('../views/home/index.vue'),
    meta: {
      requireAuth: true,    // 自定义属性，用于控制进入页面前是否需要判断登录,
      TabbarShow: true // 需要显示 底部导航
    },
  }
]

const router = new VueRouter({
  routes,
  mode: 'history',
})

export default router
