import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import api from './utils/api.js'
import Vant from 'vant';
import 'vant/lib/index.css';

Vue.use(Vant);

Vue.prototype.api = api

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  // 路由拦截
  if (to.matched.some(res => res.meta.requireAuth)) {  //验证是否需要登录
    if (sessionStorage.getItem('userId')) {  //查询本地存储信息是否已经登录
      next()
    } else {
      next({
        path: '/login',   //未登录 跳转到登录页面
        query: { redirect: to.fullpath }  //登录成功后回到当前页面，这里传值给login页面，to.fullpath为当前点击的页面
      })
    }
  } else {
    next()   //不需要登录验证，则直接跳转到目标页面
  }
  if (to.name == null || to.name == '') {
    document.title = ''
  } else {
    document.title = to.name
  }
  next()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
