import http from '../utils/http'
// 
/**
 *  @parms resquest 请求地址 例如：http://197.82.15.15:8088/request/...
 *  @param '/testIp'代表vue-cil中config，index.js中配置的代理
 */
let resquest = "https://wyy-tau.vercel.app"
// let resquest = 'http://musicapi.leanapp.cn'

export default {
  // 不同接口对应不同函数名 get（）和post（）只是示例
  // post请求示例
  // post (params) {
  //   return http.post(`${resquest}/postForm.json`, params)
  // },
  // http.get(`${resquest}/getList.json`, params)  ${resquest}接口统一地址  /getList.json具体接口地址
  // 登录
  login (params) {
    return http.get(`${resquest}/login/cellphone`, params)
  },
  //注册
  registered (params) {
    return http.get(`${resquest}/register/cellphone`, params)
  },
  // 验证码
  captcha (params) {
    return http.get(`${resquest}/captcha/sent`, params)
  },
  // 热门歌单分类
  SongList (params) {
    return http.get(`${resquest}/playlist/hot`, params)
  },
  // 歌单 ( 网友精选碟 )
  Songhot (params) {
    return http.get(`${resquest}/top/playlist`, params)
  },
  // 获取歌单详情
  Songdetails (params) {
    return http.get(`${resquest}/playlist/detail`, params)
  },
  // 获取精品歌单
  highquality (params) {
    return http.get(`${resquest}/top/playlist/highquality`, params)
  },
  // 获取推荐新音乐
  newsong (params) {
    return http.get(`${resquest}/personalized/newsong`, params)
  },
  // banner
  banner (params) {
    return http.get(`${resquest}/banner`, params)
  }
}